/* Outline and shake animation for sudoku grid */
.sudoku-grid-outline {
  display: inline-block;
}

@keyframes shake {
  0% { transform: translateX(0); }
  20% { transform: translateX(-8px); }
  40% { transform: translateX(8px); }
  60% { transform: translateX(-8px); }
  80% { transform: translateX(8px); }
  100% { transform: translateX(0); }
}

.shake {
  animation: shake 0.5s cubic-bezier(.36,.07,.19,.97) both;
}

html, body {
  margin: 0;
  padding: 0;
  height: 100dvh;
  min-height: 100dvh;
  max-height: 100dvh;
  overflow: hidden;
}

#root, .app {
  height: 100dvh;
  min-height: 100dvh;
  max-height: 100dvh;
}

:root {
  --app-bg: #fff;
  --text-color: #222;
  --grid-thick-outline-color: #333;
  --grid-outline-color: #bbb;
  --cell-size: min(10vw, 10vh);
  --sudoku-number-font-size: calc(var(--cell-size) * 0.65);
  --sudoku-note-font-size: calc(var(--cell-size) * 0.25);
  --cell-bg: #fff;
  --cell-readonly-bg: #eee;
  --cell-highlight-bg: #fff066ff;
  --cell-related-bg: #e0f7fa;
  --cell-note-color: #888;
  --cell-note-highlight: #ffff00aa;
  --cell-note-highlight-color: #280707ff;
  --numberpad-filled-bg: #e0e0e0;
  --numberpad-filled-color: #888;
}

.sudoku-grid {
  display: grid;
  grid-template-columns: repeat(9, 1fr);
  grid-template-rows: repeat(9, 1fr);
  width: 100%;
  aspect-ratio: 1 / 1;
}

.sudoku-cell {
  box-sizing: border-box;
  border: 1px solid var(--grid-outline-color);
  background: var(--cell-bg);
  display: flex;
  align-items: center;
  justify-content: center;
}

.sudoku-cell input:focus{
  outline: none;
}

@media (prefers-color-scheme: dark) {
  :root {
    --app-bg: #171717;
    --text-color: #faecd2;
    --grid-thick-outline-color: #D09858;
    --grid-outline-color: #4e3921;
    --cell-bg: #1B1B1B; 
    --cell-color: #faecd2;
    --cell-readonly-bg: #202020;
    --cell-highlight-bg: #3A4404;
    --cell-related-bg: #231800;
    --cell-note-color: #faecd288;
    --cell-note-highlight: #3A4404;
    --cell-note-highlight-color: #faecd2;
    --numberpad-filled-bg: #444;
    --numberpad-filled-color: #1B1B1B;
    --button-bg: #222;
    --button-selected-bg: #62431f;
  }
}

@media (min-width: 600px) {
  :root {
    --sudoku-number-font-size: 2em;
    --sudoku-note-font-size: 0.5em;
  }
}

@media (max-width: 600px) {
  .numberpad-root button {
    font-size: clamp(1.5rem, 5vw, 2.4em);
  }
  .numberpad-root button:last-child {
    font-size: clamp(1.3rem, 4vw, 2em);
  }
}

.App {
  background: var(--app-bg);
}

.App h1 {
  font-size: min(2.8rem, 16vw);
  color: var(--text-color);
  max-width: 600px;
  margin: 2rem auto 1rem auto;
  text-align: center;
  width: 100%;
  line-height: 1.1;
}

@media (max-width: 600px) {
  .App h1 {
    font-size: clamp(2rem, 4.67vw, 2.8rem);
  }
}

.numberpad-root,
.numberpad-container,
.numberpad-container > *,
.gameboard,
.gameboard-container {
  width: 600px;
  max-width: 600px;
  box-sizing: border-box;
}

.numberpad-root {
  width: 100%;
  max-width: 100vw;
  min-width: 0;
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  overflow-x: hidden;
}

.numberpad-root button {
  min-width: 0;
  max-width: 100%;
  box-sizing: border-box;
  flex: 1 1 0;
  width: 1%;
  border-radius: 5px !important;
  border: 1px solid var(--grid-outline-color) !important;
  box-shadow: none !important;
  outline: none;
}

.numberpad-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 24px auto 0 auto;
}

@media (max-width: 600px) {
  .numberpad-root,
  .numberpad-container,
  .numberpad-container > *,
  .gameboard,
  .gameboard-container {
    width: 98vw !important;
    max-width: 98vw !important;
    min-width: 0 !important;
    box-sizing: border-box;
  }
  
  .numberpad-root {
    flex-wrap: wrap;
    gap: 0.3rem;
  }

  .gameboard {
    aspect-ratio: 1 / 1;
  }
  .numberpad-container {
    margin: 16px auto 0 auto;
  }
}

@media (max-width: 300px) {
  .numberpad-root {
    display: grid !important;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(2, 1fr);
    gap: 0.5rem;
    width: 98vw !important;
    max-width: 98vw !important;
    min-width: 0 !important;
    box-sizing: border-box;
  }
  .numberpad-root button {
    width: 100%;
    min-width: 0;
    max-width: 100%;
    height: 3rem;
  }
}

.main-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100dvh;
  width: 100%;
  box-sizing: border-box;
  overflow: hidden;
  padding-bottom: 32px;
}

.gameboard-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  margin: 0 auto;
  box-sizing: border-box;
}

.gameboard {
  width: 100%;
  aspect-ratio: 1 / 1;
  border: 2px solid var(--grid-thick-outline-color);
  box-sizing: border-box;
}

@media (max-width: 600px) {
  .gameboard {
    width: 98vw;
    max-width: 98vw;
    min-width: 0;
  }
  .gameboard-container {
    padding: 0;
  }
}

.game-stats {
  display: flex;
  justify-content: space-between;
  font-size: min(1rem, 8vw);
  width: 600px;
  max-width: 600px;
  margin: 0.3rem auto 0 auto;
  text-align: center;
  line-height: 1.1;
  box-sizing: border-box;
  padding: 0 0.2rem;
  color: var(--text-color);
}

@media (max-width: 600px) {
  .game-stats {
    width: 98vw !important;
    max-width: 98vw !important;
    min-width: 0 !important;
    box-sizing: border-box;
  }
}
